.el-upload {
  display: inline-block;
  text-align: center;
  cursor: pointer;
  outline: 0;
}

.el-upload__input {
  display: none;
}

.el-upload__tip {
  font-size: 0.32rem;
  color: #606266;
  margin-top: 0.186667rem;
}

.el-upload iframe {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  opacity: 0;
  filter: alpha(opacity=0);
}

.el-upload--picture-card {
  background-color: #fbfdff;
  border: 0.026667rem dashed #c0ccda;
  border-radius: 0.16rem;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  width: 3.946667rem;
  height: 3.946667rem;
  line-height: 3.893333rem;
  vertical-align: top;
}

.el-upload--picture-card i {
  font-size: 0.746667rem;
  color: #8c939d;
}

.el-upload--picture-card:hover,
.el-upload:focus {
  border-color: #409EFF;
  color: #409EFF;
}

.el-upload:focus .el-upload-dragger {
  border-color: #409EFF;
}

.el-upload-dragger {
  background-color: #fff;
  border: 0.026667rem dashed #d9d9d9;
  border-radius: 0.16rem;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  width: 9.6rem;
  height: 4.8rem;
  text-align: center;
  position: relative;
  overflow: hidden;
}

.el-upload-dragger .el-icon-upload {
  font-size: 1.786667rem;
  color: #C0C4CC;
  margin: 1.066667rem 0 0.426667rem;
  line-height: 1.333333rem;
}

.el-upload-dragger+.el-upload__tip {
  text-align: center;
}

.el-upload-dragger~.el-upload__files {
  border-top: 0.026667rem solid #DCDFE6;
  margin-top: 0.186667rem;
  padding-top: 0.133333rem;
}

.el-upload-dragger .el-upload__text {
  color: #606266;
  font-size: 0.373333rem;
  text-align: center;
}

.el-upload-dragger .el-upload__text em {
  color: #409EFF;
  font-style: normal;
}

.el-upload-dragger:hover {
  border-color: #409EFF;
}

.el-upload-dragger.is-dragover {
  background-color: rgba(32,159,255,.06);
  border: 0.053333rem dashed #409EFF;
}

.el-upload-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.el-upload-list__item {
  -webkit-transition: all .5s cubic-bezier(.55,0,.1,1);
  transition: all .5s cubic-bezier(.55,0,.1,1);
  font-size: 0.373333rem;
  color: #606266;
  line-height: 1.8;
  margin-top: 0.133333rem;
  position: relative;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border-radius: 0.106667rem;
  width: 100%;
}

.el-upload-list__item .el-progress {
  position: absolute;
  top: 0.533333rem;
  width: 100%;
}

.el-upload-list__item .el-progress__text {
  position: absolute;
  right: 0;
  top: -0.346667rem;
}

.el-upload-list__item .el-progress-bar {
  margin-right: 0;
  padding-right: 0;
}

.el-upload-list__item:first-child {
  margin-top: 0.266667rem;
}

.el-upload-list__item .el-icon-upload-success {
  color: #67C23A;
}

.el-upload-list__item .el-icon-close {
  display: none;
  position: absolute;
  top: 0.133333rem;
  right: 0.133333rem;
  cursor: pointer;
  opacity: .75;
  color: #606266;
}

.el-upload-list__item .el-icon-close:hover {
  opacity: 1;
}

.el-upload-list__item .el-icon-close-tip {
  display: none;
  position: absolute;
  top: 0.133333rem;
  right: 0.133333rem;
  font-size: 0.32rem;
  cursor: pointer;
  opacity: 1;
  color: #409EFF;
}

.el-upload-list__item:hover {
  background-color: #F5F7FA;
}

.el-upload-list__item:hover .el-icon-close {
  display: inline-block;
}

.el-upload-list__item:hover .el-progress__text {
  display: none;
}

.el-upload-list__item.is-success .el-upload-list__item-status-label {
  display: block;
}

.el-upload-list__item.is-success .el-upload-list__item-name:focus,
.el-upload-list__item.is-success .el-upload-list__item-name:hover {
  color: #409EFF;
  cursor: pointer;
}

.el-upload-list__item.is-success:focus:not(:hover) .el-icon-close-tip {
  display: inline-block;
}

.el-upload-list__item.is-success:active .el-icon-close-tip,
.el-upload-list__item.is-success:focus .el-upload-list__item-status-label,
.el-upload-list__item.is-success:hover .el-upload-list__item-status-label,
.el-upload-list__item.is-success:not(.focusing):focus .el-icon-close-tip {
  display: none;
}

.el-upload-list.is-disabled .el-upload-list__item:hover .el-upload-list__item-status-label {
  display: block;
}

.el-upload-list__item-name {
  color: #606266;
  display: block;
  margin-right: 1.066667rem;
